/* base style */
.stepper {
  display: inline-block;
  max-width: 120px;
  width: 100%;
  position: relative; }

.stepper__input {
  width: 100%;
  height: 50px;
  padding: .375rem .75rem;
  border: 1px solid #c4c4c4;
  -moz-appearance: textfield; }

.stepper__input:focus {
  color: #333;
  background-color: #fff;
  border-color: #c4c4c4;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); }

.stepper__input::-webkit-inner-spin-button,
.stepper__input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }

.stepper__controls [spinner-button="up"],
.stepper__controls [spinner-button="down"] {
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 0;
  outline: none;
  border: 1px solid #c4c4c4;
  background-color: #fff;
  height: 50%;
  font-size: 1.375em;
  line-height: 0;
  transition: all ease 0.25s; }

.stepper__controls [spinner-button="up"]:hover,
.stepper__controls [spinner-button="down"]:hover {
  background-color: #333;
  border-color: #333;
  color: #fff; }

/* style 1 */
.stepper--style-1 .stepper__input {
  padding-right: 3.25rem; }

.stepper--style-1 .stepper__controls {
  position: absolute;
  right: 0;
  top: 0;
  bottom: -1px;
  display: flex;
  flex-direction: column;
  width: 2.5rem;
  z-index: 1; }

.stepper--style-1 [spinner-button="up"] {
  margin-bottom: -1px; }

/* style 2 */
.stepper--style-2 {
  max-width: 140px; }

.stepper--style-2 .stepper__input {
  padding-left: 3.25rem;
  padding-right: 3.25rem;
  text-align: center; }

