.stepper--style-2 [spinner-button="down"] {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: -1px;
  width: 2.5rem;
  z-index: 1; }

.stepper--style-2 [spinner-button="up"] {
  right: 0;
